.logo-academy {
    margin: 0;
    padding: 20px 0 20px;
    background-color:  #f2f2f2;

    @media (min-width: @t) {
        padding-top: 30px;
        width: 200px;
        padding-bottom: 30px;
    }
}

.logo-academy__link {
    margin: 0 22px 0 20px;
    padding: 0;
    list-style: none;
    text-decoration: none;
    color: #666666;
    margin-left: 20px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    @media (min-width: @t) {
        margin: 0 0 0 0;    
    }
}

.logo-academy__text {
    margin: 0;
    padding: 0;
    font-family: @lato;
    font-size: 16px;
    line-height: 20px;
    width: 113px;
    margin-top: 7px;

    @media (min-width: @t) {
        width: 115px;
        margin: 7px 0 0 36px;

        @media (min-width: @d) {
            margin: 7px 0 0 36px;        
        }
    }
}

.logo-academy__image {
    margin: 0;
    padding: 0;
}

.logo-academy__link:hover {
    color: #68b738;
}

.logo-academy__link:focus {
    color: #68b738;
}

.logo-academy__link:active {
    opacity: 0.3;
}
