.body {
  padding: 0;
  margin: 0;
  font-family: "Oswald Regular", Arial, Sans-serif;
  position: relative;
  z-index: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
