@media (min-width: 768px) and (min-width: 1440px) {
  .text-input__input {
    width: 401px;
  }
}
.text-input__input:hover {
  border: 1px solid #cdcdcd;
}
.text-input__input:focus {
  outline: none;
  border: 2px solid #444444;
}
.text-input__input:invalid:not(:placeholder-shown) {
  border: 2px solid #ff8282;
}
.text-input__input:invalid:not(:placeholder-shown) + .contacts-group__icon {
  stroke: #ff8282;
}
.radio-input__label {
  color: #222222;
  font-family: "Oswald Regular", Arial, Sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  padding-left: 44px;
  position: relative;
}
@media (min-width: 768px) {
  .radio-input__label {
    font-size: 20px;
    line-height: 22px;
    padding-left: 45px;
  }
}
.radio-input__label::before {
  content: "";
  width: 28px;
  height: 28px;
  position: absolute;
  background-color: #ffffff;
  border-radius: 20px;
  left: -1px;
  top: -3px;
  display: block;
  border: 1px solid #cdcdcd;
  cursor: pointer;
}
@media (min-width: 768px) {
  .radio-input__label::before {
    width: 38px;
    height: 38px;
    border-radius: 25px;
    left: -15px;
    top: -6px;
  }
}
.radio-input__label::after {
  content: "";
  width: 14px;
  height: 14px;
  border-radius: 7px;
  position: absolute;
  background-color: #68b738;
  left: 7px;
  top: 5px;
  display: none;
}
@media (min-width: 768px) {
  .radio-input__label::after {
    width: 18px;
    height: 18px;
    border-radius: 25px;
    left: -4px;
    top: 5px;
  }
}
.radio-input__input:checked + .radio-input__label::after {
  display: block;
}
.radio-input__input:hover + .radio-input__label::before {
  border: 1px solid #bababa;
}
.radio-input__input:disabled + .radio-input__label {
  opacity: 0.3;
}
.button-inner {
  display: block;
  font-family: "Oswald Regular", Arial, Sans-serif;
  text-decoration: none;
  min-height: 40px;
  border: none;
  box-sizing: border-box;
  background-color: #68B738;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-align: center;
  padding: 11px 64px 9px 63px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button-inner {
    font-size: 20px;
    line-height: 26px;
    padding: 14px 20px;
  }
}
.button-inner:hover {
  background-color: #5eaa2f;
}
.button-inner:active {
  color: rgba(255, 255, 255, 0.3);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.checkbox-input__label {
  font-family: "Oswald Regular", Arial, Sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  padding-left: 44px;
  position: relative;
}
@media (min-width: 768px) {
  .checkbox-input__label {
    font-size: 20px;
    line-height: 22px;
    padding-left: 60px;
  }
}
.checkbox-input__label::before {
  content: "";
  width: 28px;
  height: 28px;
  position: absolute;
  background-color: #ffffff;
  border-radius: 1px;
  left: -1px;
  top: -3px;
  display: block;
  border: 1px solid #cdcdcd;
  cursor: pointer;
}
@media (min-width: 768px) {
  .checkbox-input__label::before {
    width: 38px;
    height: 38px;
    left: 0;
    top: -5px;
  }
}
.checkbox-input__label::after {
  content: "";
  width: 15px;
  height: 8px;
  position: absolute;
  transform: rotate(-47deg);
  border-bottom: 2px solid #68B738;
  border-left: 2px solid #68B738;
  left: 6px;
  top: 4px;
  display: none;
}
@media (min-width: 768px) {
  .checkbox-input__label::after {
    width: 20px;
    height: 11px;
    left: 9px;
    top: 5px;
  }
}
.checkbox-input__input:checked + .checkbox-input__label::after {
  display: block;
}
.checkbox-input__input:hover + .checkbox-input__label::before {
  border: 1px solid #bababa;
}
.checkbox-input__input:disabled + .checkbox-input__label {
  opacity: 0.3;
}
.special-title {
  padding: 0;
