    border: 1px solid #cdcdcd;
    margin: 0 29px 0 30px;
  }
}
@media (min-width: 1440px) {
  .addition__list {
    width: 895px;
    margin: 0;
    border: none;
    border-top: 1px solid #cdcdcd;
  }
}
.addition__item {
  padding: 0;
  margin: 0;
  border-bottom: 1px solid #cdcdcd;
  padding-top: 10px;
  padding-bottom: 18px;
}
@media (min-width: 768px) {
  .addition__item {
    padding-top: 11px;
    padding-bottom: 27px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .addition__item {
    display: flex;
    flex-wrap: wrap;
    gap: 41px;
    padding-bottom: 16px;
    padding-top: 15px;
  }
}
.addition__container {
  margin: 0 20px;
}
@media (min-width: 768px) {
  .addition__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 13px;
    margin: 0 40px 22px 38px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .addition__container {
    margin: 0;
    width: 609px;
    padding-top: 8px;
  }
}
.addition__wrapper {
  padding-top: 10px;
  margin-bottom: 14px;
}
@media (min-width: 768px) {
  .addition__wrapper {
    margin: 0;
    padding: 0;
    padding-top: 5px;
    justify-content: end;
  }
}
@media (min-width: 1440px) {
  .addition__wrapper {
    justify-content: space-between;
    width: 284px;
  }
}
.addition__card-title {
  margin-top: 1px;
}
@media (min-width: 768px) {
  .addition__card-title {
    margin: 0;
  }
}
.addition__text {
  font-size: 14px;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .addition__text {
    font-size: 16px;
    text-align: right;
    min-width: 90px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .addition__text {
    text-align: left;
    min-width: 30px;
  }
}
@media (min-width: 1440px) {
  .addition__gift {
    width: 245px;
  }
}
.gift {
  padding: 0;
  margin: 0 auto;
  position: relative;
  padding-top: 35px;
}
@media (min-width: 768px) {
  .gift {
    padding-top: 68px;
    padding-bottom: 74px;
  }
}
.gift__link {
  display: block;
}
.gift__container {
  margin: 0 20px;
  background-color: #68b738;
  padding-bottom: 290px;
  margin-bottom: 36px;
}
@media (min-width: 768px) {
  .gift__container {
    padding-bottom: 200px;
    margin: 0 30px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .gift__container {
    margin: 0;
  }
}
.gift__container::before {
  content: "";
  background: url("../img/green-cat.jpg") no-repeat 0 0;
  width: 280px;
  height: 290px;
  position: absolute;
  top: 35px;
  left: 50%;
  transform: translate(-50%);
}
@media (min-width: 768px) {
  .gift__container::before {
    background: url("../../../img/gift-t.jpg");
    width: 707px;
    height: 200px;
    top: 68px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .gift__container::before {
    background: url("../img/gift-d.jpg") no-repeat 0 0;
    width: 245px;
    height: 288px;
    top: 1px;
  }
}
.gift__container::after {
  content: "";
  background: url("../img/icon_present.svg") no-repeat 0 0;
  width: 78px;
  height: 80px;
  position: absolute;
  top: 94px;
  left: 50%;
  transform: translate(-50%);
}
@media (min-width: 768px) {
  .gift__container::after {
    top: 125px;
    left: 148px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .gift__container::after {
    top: 58px;
    left: 50%;
  }
}
.gift__container .gift__text {
  font-family: "Lato", Arial, Sans-serif;
  color: #ffffff;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  position: absolute;
  bottom: 43px;
  left: 50%;
  transform: translate(-52%);
  width: 200px;
}
@media (min-width: 768px) {
  .gift__container .gift__text {
    top: 139px;
    text-align: start;
    left: calc(100% - 212px);
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .gift__container .gift__text {
    text-align: center;
    top: 185px;
    left: 52%;
  }
}
